.body {
  font-family: 'Open Sans', sans-serif;
  color: #000;
  font-weight: 400;
}
ul {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 0 0;
}
p {
  margin: 0 0 0 0;
}
fieldset {
  padding: 0 0 0 0;
  border: none;
}
figure {
  margin: 0 0 0 0;
}
img {
  object-fit: contain;
}
li {
  list-style: none;
}
.container {
  max-width: 1136px;
  margin: 0 auto;
}
.visually__hidden {
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  cursor: pointer;
  background-color: transparent;
  border: none;
}
.header__wrapper {
  background-color: #F5F5F5;
  min-height: 40px;
}
.menu__item--active {
  background-color: #FFFFFF;
  transform: matrix(1, 0, -0.6, 1, 0, 0);
}
.menu__item--active .menu__link {
  display: flex;
  transform: matrix(1, 0, 0.6, 1, 0, 0);
  color: #000;
}
.menu__link {
  padding: 10px 10px 10px 9px;
  text-decoration: none;
  color: #959595;
  font-size: 18px;
  line-height: 23.4px;
}
.menu__list {
  display: flex;
  align-items: center;
  min-height: 40px;
}
.menu__nav {
  display: flex;
  align-items: center;
  max-width: 1440px;
  margin: 0 auto;
}
.menu__list--flex {
  flex: 1;
  justify-content: center;
}
.menu__item {
  display: block;
  padding: 0 10px 0 10px;
}
.social__list {
  display: flex;
}
.social__item {
  margin-right: 16px;
}
.social__item:last-child {
  margin-right: 22px;
}
.header-advantage__svg {
  width: 48px;
  height: 48px;
}
.header-advantage__list {
  display: flex;
  align-items: center;
  margin: 28px 0 28px 0;
  padding: 0 35px 0 38px;
}
.header-advantage__figure {
  display: flex;
  align-items: center;
}
.header-advantage {
  display: flex;
  align-items: center;
  max-width: 1440px;
  margin: 0 auto;
}
.header-advantage__figcaption {
  max-width: 224px;
  margin-left: 10px;
}
.header-advantage__item {
  margin-right: 64px;
}
.header-advantage__item:last-child {
  margin-right: 0;
}
.contacts__svg {
  width: 24px;
  height: 24px;
  fill: #D93F3C;
  margin-right: 8px;
}
.contacts__link {
  font-family: 'FranklinGothicBookXCmpC', sans-serif;
  font-weight: 400;
  font-size: 44px;
  color: #D93F3C;
  text-decoration: none;
}
.contacts__item--row .contacts__link {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}
.contacts__working {
  font-size: 16px;
  line-height: 24px;
}
.contacts__callback {
  padding: 0;
  font-size: 16px;
  line-height: 24px;
  color: #D93F3C;
  text-decoration: none;
  border-top: unset;
  border-left: unset;
  border-right: unset;
  border-bottom: 1px solid #D93F3C;
  cursor: pointer;
  background-color: transparent;
  margin-left: 20px;
}
.contacts__list {
  display: flex;
  flex-direction: column;
}
.contacts__item--flex {
  display: flex;
  justify-content: space-between;
}
.contacts__list--flex {
  flex: 1;
  align-items: flex-end;
  margin-right: 33px;
}
.menu-dropdown__link {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-size: 16px;
  color: #000;
  text-decoration: none;
}
.menu-dropdown__list {
  display: flex;
  justify-content: space-around;
  max-width: 1440px;
  margin: 0 auto;
}
.menu-dropdown__svg {
  width: 13px;
  height: 8px;
  margin-left: 8px;
}
.menu-dropdown--shadow {
  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.25);
}
.menu-dropdown__item {
  padding: 16px 0 16px 0;
}
.breadcramps__list {
  display: flex;
  align-items: center;
}
.breadcramps__link {
  font-size: 18px;
  line-height: 24px;
  color: #A4A4A4;
}
.breadcramps__item {
  margin-right: 10px;
}
.general {
  margin-top: 10px;
}
.content__box {
  margin: 40px 0 20px 0;
}
@media (min-width: 1440px) {
  .content__block {
    display: none;
  }
}
.content__inner {
  position: relative;
  margin-top: 46px;
}
.content__inner::after {
  content: '';
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  border-left: 4px solid #D93F3C;
  top: 0;
  height: 100%;
}
.heading__h1 {
  font-family: 'FranklinGothicBookXCmpC', sans-serif;
  font-weight: 400;
  font-size: 56px;
}
.heading__red {
  color: #D93F3C;
}
.values__description {
  font-size: 20px;
  font-weight: 400;
  margin: 0 0 15px 0;
}
.values__list {
  margin-left: 20px;
}
